import React, { useState } from 'react';

// import MyLogo from '../../assets/logo_draft_no_color_opt.svg';
import {ReactComponent as MyLogoMB} from '../../assets/logo_draft3_MB.svg';
import {ReactComponent as MyLogo} from '../../assets/logo_draft2_no_color.svg';
import './SideBar.css';


const SideBar = () => {

    const [showLogo, setShowLogo] = useState(false);

    const mbOn = () => {
        setShowLogo(true);
    }

    const mbOff = () => {
        setShowLogo(false);
    }

    return(
        <div className="SideBarContainer" >
            <div className="Logo" onMouseEnter={mbOn} onMouseLeave={mbOff}>
                {showLogo &&
                    <MyLogoMB width='150' height='150' />
                }
                {!showLogo &&
                    <MyLogo fill='none' stroke='white' width='150' height='150' />
                }
            </div>
            <ul>
                <li><a href="#section1">Home</a></li>
                <hr className="HRDivider"/>
                <li><a href="#section2">About Me</a></li>
                <hr className="HRDivider"/>
                <li><a href="#section3">Section 3</a></li>
                <hr className="HRDivider"/>
                <li><a href="#section4">Section 4</a></li>
                <hr className="HRDivider"/>
                <li><a href="#section5">Section 5</a></li>
                <hr className="HRDivider"/>
            </ul>
        </div>
    )
}

export default SideBar;